.item {
  position: relative;
  width: calc((100% - 20px * 3) / 4);

  .thumbs img {
    display: block;
    width: 100%;
    height: auto;
  }

  .overlay {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(0 0 0 / 0.5);
    color: white;
    opacity: 0;
    transition: 0.3s ease;

    .title {
      font-size: 30px;
      text-align: center;
      transform: translateY(20px);
      opacity: 0;
      transition-duration: 0.3s;
      transition-timing-function: ease;
      transition-delay: 0.1s;

      & a {
        color: white;
        text-decoration: none;
        word-break: keep-all;
      }
    }

    .year {
      margin-top: 10px;
      transform: translateY(20px);
      opacity: 0;
      transition-duration: 0.3s;
      transition-timing-function: ease;
      transition-delay: 0.2s;
    }

    .genres {
      display: flex;
      justify-content: center;
      gap: 15px;
      margin-top: 10px;
      font-size: 14px;
      transform: translateY(20px);
      opacity: 0;
      transition-duration: 0.3s;
      transition-timing-function: ease;
      transition-delay: 0.3s;
    }

    &:hover .title,
    &:hover .year,
    &:hover .genres {
      transform: translateY(0px);
      opacity: 1;
    }
  }

  &:hover .overlay {
    opacity: 1;
  }
}
