.soundOnly {
  overflow: hidden;
  display: inline;
  position: absolute;
  top: 0;
  left: 0;
  width: 1px;
  height: 1px;
  clip: rect(1px, 1px, 1px, 1px);
}

.btnExit {
  position: absolute;
  top: 30px;
  right: 30px;
  z-index: 1;

  & a {
    position: relative;
    display: block;
    width: 30px;
    height: 30px;

    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      width: 100%;
      height: 2px;
      margin-top: -1px;
      background-color: white;
      transform: translateX(-50%) rotate(45deg);
    }
    &::after {
      transform: translateX(-50%) rotate(-45deg);
    }
  }
}

.movie {
  position: relative;

  .visual {
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 100%;
    height: 100dvh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    color: white;

    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgb(0 0 0 / 0.5);
    }

    /* & img {
    position: absolute;
    width: 100%;
    z-index: 1;
  } */

    .info {
      position: absolute;
      top: 50%;
      left: 0;
      width: 100%;
      padding: 0 30px;
      text-align: center;
      transform: translateY(-50%);

      .title {
        font-size: 80px;
      }

      .details {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 20px;
        margin-top: 30px;

        & dl {
          display: flex;
          gap: 10px;

          .ratingWrap {
            display: flex;
            gap: 5px;
            align-items: center;

            .rating {
              position: relative;

              .stars {
                display: flex;
                color: #eee;

                &.fill {
                  position: absolute;
                  top: 0;
                  left: 0;
                  width: 100%;
                  color: #ffc107;
                }

                .star {
                  overflow: hidden;
                  display: block;
                  width: 16px;
                  line-height: 1;
                }
                .clip {
                  clip-path: polygon(
                    0% 0%,
                    var(--clip) 0%,
                    var(--clip) 100%,
                    0% 100%
                  );
                }
              }
            }
          }
        }
      }

      .desc {
        overflow: hidden;
        overflow-y: auto;
        width: 100%;
        max-width: 768px;
        max-height: 500px;
        margin: 50px auto 0;
        text-align: left;
        line-height: 1.6;
      }

      .genres {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 10px;

        & li:not(:last-child)::after {
          content: "/";
          margin: 0 4px;
        }
      }
    }
  }
}
